.Container {
	margin: 60px;
}

.Title {
	display: block;
	font-size: 34px;
	font-weight: 800;
	line-height: 40px;
	letter-spacing: 0em;
	text-align: right;
	padding-bottom: 20px;
}

.QuestionsList {
	padding: 0;
	display: flex;
	flex-flow: wrap;
}

.Line {
	margin-left: 20px;
}

.SubText {
	font-size: 12px;
	font-weight: 500;
	line-height: 24px;
	letter-spacing: 0em;
	text-align: right;
}

.SubText1 {
	display: inline;
	color: #b0bf24;
}

@media (max-width: 500px) {
	.Title {
		font-size: 24px;
	}

	.Container {
		margin: 15px;
	}

	.Line {
		margin-left: 0;
	}
}
