.FaqItem {
	overflow: hidden;
	margin: 8px 0;
	margin-left: 20px;
	display: inline-block;
	width: 48%;
	background: #ffffff;
	box-shadow: 0px 4px 16px rgba(137, 137, 137, 0.1);
	border-radius: 8px;
	align-self: flex-start;
}

.FaqQuestion {
	position: relative;
	border-radius: 12px;
	color: var(--neutral900Color);
	font-size: 16px;
	line-height: 24px;
	padding: 20px;
	padding-left: 60px;
	margin-bottom: 0;
	background: var(--background3Color);
	box-shadow: 1px 1px 4px rgb(36 36 36 / 8%), 0 0 8px rgb(36 36 36 / 8%);
	cursor: pointer;
	font-weight: 600;
}

.FaqItemBtn {
	position: absolute;
	top: 50%;
	left: 12px;
	display: flex;
	width: 20px;
	height: 48px;
	align-items: center;
	justify-content: center;
	transform: translateY(-50%) rotate(90deg);
}

.FaqItemBtnIcon {
	display: block;
	width: 24px;
	height: 24px;
	fill: var(--blackColor);
	transition: transform 0.25s;
}

.FaqAnswerClose {
	max-height: 0;
	transition: max-height 0.35s;
}

.FaqAnswerOpen {
	max-height: 500px;
	transition: max-height 0.35s;
}

.FaqAnswerText {
	color: var(--neutral700Color);
	font-size: 14px;
	letter-spacing: -0.01em;
	line-height: 1.43;
	text-align: justify;
	padding: 10px 24px;
}

@media (max-width: 500px) {
	.FaqQuestion {
		font-size: 14px;
		padding-left: 45px;
	}

	.FaqAnswerText {
		font-size: 13px;
	}

	.FaqItem {
		margin-bottom: 8px;
		width: 100%;
		margin-left: 0;
	}

	.FaqItemBtn {
		left: 10px;
	}
}
